$primary: #3caea3;
$secondary: #f6d55c;
.nav {
  width: 100%;
  height: 60px;
  position: fixed;
  bottom: 20px;
  z-index: 1000;

  .navigation {
    margin: auto;
    width: 300px;
    height: 100%;
    border-radius: 30px;
    padding: 0 10px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    background: rgba(75, 75, 75, 0.36);
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(8.8px);
    -webkit-backdrop-filter: blur(8.8px);
    .nav-item {
      width: 42px;
      cursor: pointer;
      height: 70%;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 25px;
      list-style: none;
      a {
        text-decoration: none;
        color: $secondary;
        display: flex;
        align-items: center;
        justify-content: center;
      }

      &:hover {
        background-color: $primary;
      }
    }
    .activ {
      background-color: #fff;
    }
  }
}
@media only screen and (max-width: 320px) {
  .nav {
    .navigation {
      width: 260px;
    }
  }
}
