.experience{width:100%;height:auto;padding:0px 10vw;padding-top:10px}.experience .name{width:100%;height:70px;line-height:5px;color:#3f5250}.experience .name h2{color:#f6d55c}.experience .content{display:flex;justify-content:space-evenly;height:400px;width:100%}.experience .content .dev{height:fit-content;width:45%;padding:0 20px;border-radius:20px;border:2px solid #f6d55c;position:relative;color:#f6d55c}.experience .content .dev .skil{text-align:start}.experience .content .dev:hover{background-color:teal;border:none}.experience .content .dev ul{height:270px;width:100%;display:flex;flex-direction:column;flex-wrap:wrap;list-style:symbols(fixed "⛔")}.experience .content .dev ul li{max-width:fit-content;margin-right:20px;margin-bottom:10px;line-height:10px}@media only screen and (max-width: 1023px){.experience .content{margin:auto;width:400px;height:850px;flex-direction:column}.experience .content .dev{width:100%}}@media only screen and (max-width: 425px){.experience{padding:0 3vw;padding-top:10px}.experience .content{margin:auto;width:300px;height:850px;flex-direction:column}.experience .content .dev{width:100%;padding:0 7px}.experience .content .dev h2{font-size:20px}.experience .content .dev h4{font-size:15px}.experience .content .dev p{font-size:10px}}