$primary: #3caea3;
$secondary: #f6d55c;
$font: #3f5250;

.portfolio {
  width: 100%;
  height: auto;
  padding: 0px 10vw;
  padding-top: 10px;
  .name {
    width: 100%;
    height: 70px;
    line-height: 5px;
    color: $font;
    h2 {
      color: $secondary;
    }
  }
  .content {
    height: auto;
    width: 100%;
    padding: 0 40px;
    display: flex;
    justify-content: center;

    .wrap-card {
      margin: auto;
      width: fit-content;
      display: flex;
      justify-content: flex-start;
      flex-wrap: wrap;
    }
  }
}
@media only screen and (min-width: 1200px) {
}
@media only screen and (max-width: 900px) {
  .portfolio {
    border: 1px solid black;
    width: 100%;
    height: auto;
    padding: 0px 10vw;
    padding-top: 10px;
    .name {
      width: 100%;
      height: 70px;
      line-height: 5px;
      color: $font;
      h2 {
        color: $secondary;
      }
    }
    .content {
      height: auto;
      width: 100%;
      padding: 0 40px;
      display: flex;
      justify-content: center;

      .wrap-card {
        margin: auto;
        width: 450px;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
      }
    }
  }
}
@media only screen and (max-width: 540px) {
  .portfolio {
    width: 100%;
    height: auto;
    padding: 0px 10vw;
    padding-top: 10px;
    .name {
      width: 100%;
      height: 70px;
      line-height: 5px;
      color: $font;
      h2 {
        color: $secondary;
      }
    }
    .content {
      height: auto;
      width: 100%;
      padding: 0 40px;
      display: flex;
      justify-content: center;

      .wrap-card {
        margin: auto;
        width: 200px;
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
      }
    }
  }
}
